import {
  Input,
  Button,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link, useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { register } from "../../services/actions/authentication";
import registerStyles from "./Register.module.css";
import { useForm } from "../../hooks/useForm";

export function Register() {
  const dispatch = useDispatch();
  const history = useHistory();

  const { values, handleChange } = useForm({
    email: "",
    password: "",
    name: "",
  });

  const error = useSelector((state) => state.auth.error);

  const onSubmit = (e) => {
    e.preventDefault();
    dispatch(register(values.name, values.email, values.password)).then(() =>
      history.push("/")
    );
  };

  return (
    <section className={registerStyles.content_box}>
      <form className={registerStyles.wrapper} onSubmit={onSubmit}>
        <p className="text text_type_main-medium"></p>
        {error && <p className="text text_type_main-default">{`${error}`}</p>}
        <div className={`${registerStyles.input_wrapper} mt-6`}>
          <Input
            placeholder=""
            name="name"
            type="text"
            value={values.name}
            onChange={handleChange}
          />
        </div>
        <div className="mt-6">
          <Input
            placeholder="E-mail"
            name="email"
            type="email"
            value={values.email}
            onChange={handleChange}
          />
        </div>
        <div className="mt-6">
          <Input
            placeholder=""
            name="password"
            type="password"
            icon={"ShowIcon"}
            value={values.password}
            onChange={handleChange}
          />
        </div>
        <div className="mt-6">
          <Button htmlType="submit"></Button>
        </div>
        <p className="text text_type_main-default text_color_inactive mt-20">
           ?{" "}
          <Link className="text text_type_main-default" to="/login">
            
          </Link>
        </p>
      </form>
    </section>
  );
}
